@font-face {
  font-family: 'MyFont';
  src: url(./font/futura.ttf) format('truetype');
}

html {
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background: radial-gradient(rgb(255, 255, 193), #ffff99);
}

.title {
  justify-content: center;
  align-items: center;
  font-size: 70px;
  font-family: 'myfont', sans-serif;
  text-align: center;
  margin: 3rem;
}

.aboutMe {
  display: flex;
  flex-direction: column;
  margin: 3rem;
}

.name {
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 50px;
}

.myIconImage {
  margin: 0 auto;
  padding: 0;
  border-radius: 50%;
  width: 200px;
  height: 200px;
}

.allContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem;
}

.container {
  display: flex;
  align-items: center;
  width: fit-content;
  margin: auto;
}


/* make */
.makeList {
  width: 40%;
}

.makeContent {
  background-color: white;
  margin-top: 2rem;
  padding: 1rem;
}

/* work */
.workList {
  width: 40%;
}

.workContent {
  background-color: white;
  margin-top: 2rem;
  padding: 1rem;
}

/* skill */
.skillList {
  width: 40%;
}

.skillContent {
  background-color: white;
  margin-top: 2rem;
  padding: 1rem;
}

/* like */
.likeList {
  width: 40%;
}

.likeContent {
  background-color: white;
  margin-top: 2rem;
  padding: 1rem;
}

/* link */
.linkList {
  width: 40%;
}

.linkContent {
  background-color: white;
  margin-top: 2rem;
  padding: 1rem;
}

.positionInput {
  display: flex;
  align-items: center;
  margin: auto;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  background-color: white;
  background-image: url('./image/searchicon.png');
  background-position: 10px 10px;
  background-repeat: no-repeat;
  padding: 12px 20px 12px 40px;
  width: 30%;
}


a {
  width: 70px;
  height: 70px;
  margin: 30px;
}

img {
  width: 100%;
  height: 100%;
}

.icons {
  color: black;
  font-size: 70px;
  margin: 0;
}

@media screen and (max-width: 767px) {
  .title {
    top: 150px;
    justify-content: center;
    align-items: center;
    font-size: 40px;
    text-align: center;
  }

  .aboutMe {
    display: flex;
    flex-direction: column;
    margin: 3rem;
  }

  .name {
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 40px;
  }

  .myIconImage {
    margin: 0 auto;
    padding: 0;
    border-radius: 50%;
    width: 150px;
    height: 150px;
  }

  .allContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2rem;
  }

  .container {
    display: flex;
    align-items: center;
    width: fit-content;
    margin: auto;
  }

  /* make */
  .makeList {
    width: 100%;
  }

  .makeContent {
    background-color: white;
    margin-top: 2rem;
    padding: 1rem;
  }

  /* work */
  .workList {
    width: 100%;
  }

  .workContent {
    background-color: white;
    margin-top: 2rem;
    padding: 1rem;
  }

  /* skill */
  .skillList {
    width: 100%;
  }

  .skillContent {
    background-color: white;
    margin-top: 2rem;
    padding: 1rem;
  }

  /* like */
  .likeList {
    width: 100%;
  }

  .likeContent {
    background-color: white;
    margin-top: 2rem;
    padding: 1rem;
  }

  /* link */
  .linkList {
    width: 100%;
  }

  .linkContent {
    background-color: white;
    margin-top: 2rem;
    padding: 1rem;
  }

  .positionInput {
    display: flex;
    align-items: center;
    margin: auto;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    background-color: white;
    background-image: url('./image/searchicon.png');
    background-position: 10px 10px;
    background-repeat: no-repeat;
    padding: 12px 20px 12px 40px;
    width: 80%;
  }

  a {
    width: 55px;
    height: 55px;
    margin: 15px;
  }

  img,
  i::before {
    margin: 0px;
  }

  .icons {
    font-size: 55px;
    margin: 0px;
  }
}